'use client';

import { useEffect } from 'react';
import Icon from '../Icon';
import * as Types from '../../core/types';
import { ua_codeblockCopy } from './codeblock';
import Prism from 'prismjs';
import 'prismjs/plugins/autoloader/prism-autoloader';

// import 'https://unpkg.com/prismjs/components/prism-core.min.js';
// import 'https://unpkg.com/prismjs/plugins/autoloader/prism-autoloader.min.js';

export interface CodeBlockProps {
  /** Language of the CodeBlock */
  lang: Types.codeLanguage;
  /** Caption for the CodeBlock */
  caption?: string;
  /** Code Syntax of the CodeBlock */
  children: React.ReactNode;
  /** Code custom class */
  className?: string;
}

/** The code block is use to display a code snippets with language and copy button */
export const CodeBlock = (props: CodeBlockProps) => {
  const className = props.className ? ' ' + props.className : '';
  const captionText = props.caption ? props.caption : props.lang;
  useEffect(() => {
    if (Prism) {
      Prism.plugins.autoloader.languages_path = 'https://unpkg.com/prismjs/components/';
      Prism.highlightAll(); // Highlights the code Prism CSS
    } else if (window.Prism) {
      window.Prism.plugins.autoloader.languages_path = 'https://unpkg.com/prismjs/components/';
      window.Prism.highlightAll(); // Highlights the code Prism CSS
    }
  }, []);

  return (
    <figure className={'ua_code' + className}>
      <figcaption>
        <span>{captionText}</span>
        <button className="copy-to-clipboard-button" type="button" onClick={ua_codeblockCopy}>
          <span>Copy </span>
          <Icon name="copy"></Icon>
        </button>
      </figcaption>
      <pre>
        <code className={`language-${props.lang}`} data-dependencies={`${props.lang}`}>
          {props.children}
        </code>
      </pre>
    </figure>
  );
};

export default CodeBlock;
