export interface PageProps {
  /** The title of the page */
  title: string;
  /** The page content */
  children: React.ReactNode;
  /** The page hero content - must include an h1 element */
  hero?: React.ReactNode;
  /** Supplementary navigation */
  subNav?: React.ReactNode;
  /** The page sidebar nav */
  sidebar?: React.ReactNode;
}

/** The `Page` component takes metadata about the page and handles in page specific layout. The root element of this component is `main`. */
export const Page = (props: PageProps) => {
  return (
    <main id="skip-link-target" className="ua_page">
      {props.hero ? <header className="ua_page_header">{props.hero}</header> : null}

      {props.subNav ? <div className="ua_page_sub-nav ua_layout--standard">{props.subNav}</div> : null}

      <div className="ua_page_content ua_layout--flow">
        {props.hero ? null : <h1>{props.title}</h1>}
        <div className="ua_layout--standard ua_layout--flow">{props.children}</div>
      </div>

      {props.sidebar ? (
        <div className="ua_page_sidebar ua_layout--flow ua_layout--standard">{props.sidebar}</div>
      ) : null}
    </main>
  );
};

export default Page;
