import { Icon } from '../../components/Icon';

export interface PaginationProps {
  /** The current page number */
  page: number;
  /** The number of items on the page */
  pageSize: number;
  /** An array of page size options */
  pageSizes?: number[];
  /** The total number of items */
  itemCount: number;
  /** Change handler */
  changeHandler: (page: number, pageSize: number) => void;
}

export const Pagination = (props: PaginationProps) => {
  const totalPages = Math.ceil(props.itemCount / props.pageSize);
  const endRange = props.page * props.pageSize;
  const startRange = props.page * props.pageSize - (props.pageSize - 1);

  const sizeChange = (value: number) => {
    const page = Math.ceil(startRange / value);
    props.changeHandler(page, value);
  };

  const pages: React.ReactElement[] = [];

  for (let page = 1; page <= totalPages; page++) {
    pages.push(<option selected={page === props.page}>{page}</option>);
  }

  const sizeOptions: React.ReactElement[] = [];

  if (props.pageSizes) {
    props.pageSizes.forEach((size) => {
      sizeOptions.push(
        <option selected={size === props.pageSize} value={size}>
          {size}
        </option>
      );
    });
  }

  return (
    <div className="ua_component_wrapper">
      <nav aria-label="Pagination" className="ua_pagination">
        <div>
          {props.pageSizes ? (
            <>
              <label htmlFor="perPageInput">Items per page:</label>
              <select name="perPage" id="perPageInput" onChange={(event) => sizeChange(Number(event.target.value))}>
                {sizeOptions}
              </select>
            </>
          ) : (
            <p>Items per page: {props.pageSize}</p>
          )}
          <p>
            {startRange} - {endRange > props.itemCount ? props.itemCount : endRange} of {props.itemCount} items
          </p>
        </div>
        <div>
          <label htmlFor="pageSelect">Page</label>
          <select
            name="pageSelect"
            id="pageSelect"
            onChange={(event) => props.changeHandler(Number(event.target.value), props.pageSize)}
          >
            {pages}
          </select>
          <p> of {totalPages}</p>

          <ul>
            <li>
              <button disabled={props.page === 1} onClick={() => props.changeHandler(props.page - 1, props.pageSize)}>
                <Icon name="arrow-left" /> Previous
              </button>
            </li>
            <li>
              <button
                disabled={props.page === totalPages}
                onClick={() => props.changeHandler(props.page + 1, props.pageSize)}
              >
                Next <Icon name="arrow-right" />
              </button>
            </li>
          </ul>
        </div>
      </nav>
    </div>
  );
};
