import { Grid } from '../..';

export interface SwatchProps {
  /** Name of the swatch */
  name: string;
  /** Swatch hex code */
  hex: string;
}

export const Swatch = (props: SwatchProps) => {
  const { name } = props;
  const { hex } = props;

  function hexToRgb(hex: string) {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result
      ? {
          r: parseInt(result[1], 16),
          g: parseInt(result[2], 16),
          b: parseInt(result[3], 16),
        }
      : {
          r: 0,
          g: 0,
          b: 0,
        };
  }

  const RED = 0.2126;
  const GREEN = 0.7152;
  const BLUE = 0.0722;
  const GAMMA = 2.4;

  function luminance(r: number, g: number, b: number) {
    const a = [r, g, b].map((v) => {
      v /= 255;
      return v <= 0.03928 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, GAMMA);
    });
    return a[0] * RED + a[1] * GREEN + a[2] * BLUE;
  }

  function contrast(rgb1: [number, number, number], rgb2: [number, number, number]) {
    const lum1 = luminance(...rgb1);
    const lum2 = luminance(...rgb2);
    const brightest = Math.max(lum1, lum2);
    const darkest = Math.min(lum1, lum2);
    return Math.floor(((brightest + 0.05) / (darkest + 0.05)) * 100) / 100;
  }

  const txtColor = contrast([0, 0, 0], [hexToRgb(hex).r, hexToRgb(hex).g, hexToRgb(hex).b]) > 4.5 ? 'black' : 'white';
  const whiteContrast = contrast([255, 255, 255], [hexToRgb(hex).r, hexToRgb(hex).g, hexToRgb(hex).b]);
  const blackContrast = contrast([0, 0, 0], [hexToRgb(hex).r, hexToRgb(hex).g, hexToRgb(hex).b]);

  const items = [
    <>
      <span>HEX:</span>
      <span>{hex}</span>
    </>,
    <>
      <span>RBG:</span>
      <span>
        {hexToRgb(hex).r}, {hexToRgb(hex).g}, {hexToRgb(hex).b}
      </span>
    </>,
    <>
      <span>WHITE CONTRAST:</span>
      <span>
        {whiteContrast >= 4.5 ? <>PASS {''}</> : <>FAIL {''}</>}({whiteContrast})
      </span>
    </>,
    <>
      <span>BLACK CONTRAST: </span>
      <span>
        {blackContrast >= 4.5 ? <>PASS {''}</> : <>FAIL {''}</>}({blackContrast})
      </span>
    </>,
  ];

  return (
    <div className="ua_component_wrapper">
      <div className="swatch">
        <h3 style={{ color: txtColor, backgroundColor: hex }}>{name}</h3>

        <Grid element="ul" items={items} />
      </div>
    </div>
  );
};
