export interface AccordionProps {
  /** Accordion Summary */
  summary: string;
  /** Accordion Content */
  children: React.ReactNode;
  /** Accordion Custom Class(es) */
  className?: string;
}

/** Accordion component can display summary and content with open and closed arrow options */
export const Accordion = (props: AccordionProps) => {
  const className = props.className ? ' ' + props.className : '';
  return (
    <div className={'ua_component_wrapper' + className}>
      <details className="ua_accordion">
        <summary>{props.summary}</summary>
        <div className="details-content">{props.children}</div>
      </details>
    </div>
  );
};

export default Accordion;
