import * as Types from '../../core/types';
import { Heading } from '../../base/Heading';
import { Icon } from '../Icon';

export interface CalloutProps {
  /** The Callout title. */
  title: string;
  /** The heading level the callout title should be. */
  heading_level: Types.heading_level;
  /** The Callout's content. */
  children: React.ReactNode;
  /** The Callout's context. */
  context?: Types.context;
  /** The Callout's custom class */
  className?: string;
}

/** Callout box is to show the information with category */
export const Callout = (props: CalloutProps) => {
  const className = props.className ? ' ' + props.className : '';
  const context_css = props.context ? 'ua_context--' + props.context : undefined;
  let icon = undefined;
  if (props.context && props.context == 'info') {
    icon = <Icon name="circle-info" alt="Info" />;
  } else if (props.context && props.context == 'positive') {
    icon = <Icon name="circle-check" alt="check mark" />;
  } else if (props.context && props.context == 'negative') {
    icon = <Icon name="circle-exclamation" alt="exclamation" />;
  }
  return (
    <div className={'ua_component_wrapper' + className}>
      <div className={`ua_callout ${context_css} ua_layout--flow`}>
        <Heading level={props.heading_level} className="ua_callout_title">
          {icon} {props.title}
        </Heading>
        {props.children}
      </div>
    </div>
  );
};

export default Callout;
