'use client';

import { useEffect } from 'react';
import { Icon } from '../../components/Icon';
import { ua_handlePrimaryNav } from './primary-nav';

export interface PrimaryNavItem {
  label: string;
  url: string;
  children?: {
    label: string;
    url: string;
  }[];
}

/** This is the Primary Navigation for every UA webpage. */
export const PrimaryNavigation = (props: { menuItems: PrimaryNavItem[] }) => {
  useEffect(() => {
    ua_handlePrimaryNav();
  }, []);

  return (
    <nav aria-label="Primary Navigation" className="ua_primary-navigation" id="UA_PrimaryNav">
      <ul className="ua_primary-navigation_list">
        {props.menuItems.map((menuItem) => (
          <li
            key={`primary-nav_${props.menuItems.indexOf(menuItem)}_${menuItem.url}`}
            className={menuItem.children ? 'ua_menu-item-parent' : ''}
          >
            <a href={menuItem.url}>{menuItem.label}</a>
            {menuItem.children ? (
              <>
                <button hidden>
                  <span className="ua_primary-navigation_inactive_content">
                    <Icon name="caret-down" alt={'Expand ' + menuItem.label + ' menu'} />
                  </span>
                  <span className="ua_primary-navigation_active_content">
                    <Icon name="caret-up" alt={'Close ' + menuItem.label + ' menu'} />
                  </span>
                </button>
                <ul aria-hidden="true" aria-label={menuItem.label + ' submenu'}>
                  {menuItem.children.map((nestedItem) => (
                    <li key={`primary-nav_sub-menu_${menuItem.children?.indexOf(nestedItem)}_${nestedItem.url}`}>
                      <a href={nestedItem.url}>{nestedItem.label}</a>
                    </li>
                  ))}
                </ul>
              </>
            ) : null}
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default PrimaryNavigation;
