import Link from '../../base/Link';
import * as Types from '../../core/types';

export interface CTAProps {
  /** The label displayed on the CTA */
  text: string;
  /** The CTA link destination */
  href: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  /** The CTA alignment */
  justify?: Types.justify;
  /** Custom class name for component container */
  className?: string;
  /** CTA presence */
  presence?: 'subtle';
  /** Determines if the CTA is Disabled */
  disabled?: boolean;
}

/** A call to action, or CTA, is a key interaction element that brings actions or points of interest to the user's attention. */
export const CallToAction = (props: CTAProps) => {
  const className = props.className ? ' ' + props.className : '';
  const justifyCSS = props.justify ? 'ua_justify--' + props.justify : '';
  let presence = '';
  if (props.presence === 'subtle') {
    presence = ' ua_presence--subtle';
  }
  let disabled = '';
  if (props.disabled) {
    disabled = 'ua_cta--disabled';
  }
  return (
    <div className={`ua_component_wrapper ${justifyCSS + className + presence}`}>
      {props.disabled ? (
        <span className={`ua_cta ${disabled}`}>{props.text}</span>
      ) : (
        <Link className={`ua_cta`} href={props.href} newTab={props.newTab}>
          {props.text}
        </Link>
      )}
    </div>
  );
};

export default CallToAction;
