import * as Types from '../../core/types';

export interface DataHighlightProps {
  /** The statistic to display */
  title: string;
  /** Information about the statistic */
  description: string;
  /** Lead-in to the statistic */
  lead?: string;
  /** */
  align?: Types.align;
  /** Custom class name */
  className?: string;
}

/** Data Highlights can be used to highlight various statistical points of interests. The combined lead, title, and description must read as a single sentence. */
export const DataHighlight = (props: DataHighlightProps) => {
  const className = props.className ? ' ' + props.className : '';
  const align = props.align ? ' ua_align--' + props.align : '';

  return (
    <div className={'ua_component_wrapper' + className}>
      <p className={`ua_data-highlight ${align}`}>
        {props.lead ? <span className="ua_data-highlight_lead">{props.lead}</span> : null}
        <strong className="ua_data-highlight_stat">{props.title}</strong>
        <span className="ua_data-highlight_description">{props.description}</span>
      </p>
    </div>
  );
};

export default DataHighlight;
