export interface GridProps {
  /**  Items to be displayed inside the Grid */
  items: React.ReactNode[];
  /**  HTML element the Grid should use as a base */
  element: 'ul' | 'ol' | 'div';
  /** Limits the number columns the Gird component will create */
  maxColumns?: number;
  /** Additional CSS classes to pass to the root Grid element */
  className?: string;
}

/** The `Grid` component is a generic layout tool that can be used to arrange almost any kind of content into columns that fluidly wrap based on content and container width. */
export const Grid = (props: GridProps) => {
  //const element = props.element ? props.element : 'div';
  const classes = props.className ? ' ' + props.className : '';
  const columns = { '--grid-column-count': props.maxColumns } as React.CSSProperties;
  const _style = props.maxColumns ? columns : undefined;
  return (
    <props.element className={'ua_layout--grid' + classes} style={_style}>
      {props.items.map((item) =>
        props.element === 'ol' || props.element === 'ul' ? (
          <li key={`GridList_${props.items.indexOf(item)}`}>{item}</li>
        ) : (
          <div key={`GridList_${props.items.indexOf(item)}`}>{item}</div>
        )
      )}
    </props.element>
  );
};

export default Grid;
