export interface LinkProps {
  children: React.ReactNode;
  href: string;
  newTab?: boolean;
  className?: string;
  rel?: string;
}

export const Link = (props: LinkProps) => {
  const className = props.className ? { className: props.className } : '';
  if (props.newTab) {
    return (
      // eslint-disable-next-line react/jsx-no-target-blank
      <a href={props.href} target="_blank" rel={props.rel ? props.rel : 'noreferrer'} {...className}>
        {props.children}
      </a>
    );
  }
  return (
    <a href={props.href} rel={props.rel} {...className}>
      {props.children}
    </a>
  );
};

export default Link;
