'use client';

import { useEffect, useState } from 'react';
import { Grid, Event, Heading, CallToAction } from '../../';
import * as Types from '../../core/types';

export type LocalistEvent = {
  id: number;
  title: string;
  localist_url: string;
  event_instances: {
    event_instance: {
      id: number;
      start: string;
      end: string;
    };
  }[];
  address?: string;
};

export type LocalistDepartment = {
  id: number;
  name: string;
  urlname: string;
  localist_url: string;
};

export interface EventFeedProps {
  title: string;
  limit: number;
  heading_level?: 1 | 2 | 3 | 4 | 5;
  maxColumns?: number;
  filters?: string;
  align?: Types.align;
  groupSlug?: string;
  className?: string;
}

/** The EventFeed component can be used to display an event's details with link to more information about the event. */
export const EventFeed = (props: EventFeedProps) => {
  const className = props.className ? ' ' + props.className : '';

  const defaultEventList: LocalistEvent[] = [];
  const defaultDepartmentList: LocalistDepartment[] = [];
  const [events, setEvents] = useState(defaultEventList);
  const [departments, setDepartments] = useState(defaultDepartmentList);
  const [calendarURL, setCalendarURL] = useState('https://calendar.ua.edu');
  const today = new Date();
  const month = today.getMonth() + 1;
  const year = today.getFullYear() + 1;
  const date = today.getDate();
  const end = '&end=' + year + '-' + month + '-' + date;

  useEffect(() => {
    const _departments: LocalistDepartment[] = [];
    fetch('https://calendar.ua.edu/api/2/departments?pp=100')
      .then((res) => {
        return res.json();
      })
      .then((json) => {
        for (let i = 0; i < json.departments.length; i++) {
          _departments.push(json.departments[i].department);
        }
        fetch('https://calendar.ua.edu/api/2/departments?pp=100&page=2')
          .then((res) => {
            return res.json();
          })
          .then((json) => {
            for (let i = 0; i < json.departments.length; i++) {
              _departments.push(json.departments[i].department);
            }
            setDepartments(_departments);
            //console.log(departments);
          });
      });
  }, []);

  useEffect(() => {
    const query = props.filters ? '&type=' + props.filters : '';
    const perPage = props.limit ? '&pp=' + props.limit : '';

    if (props.groupSlug && departments.length > 0) {
      let groupID = null;
      // Find the Group ID based on Group's URLName
      const filteredDepartment = departments.filter((department) => department.urlname === props.groupSlug);
      //console.log(filteredDepartment);
      if (filteredDepartment.length > 0) {
        groupID = filteredDepartment[0].id;
        setCalendarURL(filteredDepartment[0].localist_url);
        const group = '&group_id=' + groupID;
        fetch('https://calendar.ua.edu/api/2/events/?require_all=true' + perPage + query + group + end)
          .then((response) => {
            return response.json();
          })
          .then((json) => {
            const _events = [];
            for (let i = 0; i < json.events.length; i++) {
              _events.push(json.events[i].event);
            }
            setEvents(_events);
          });
      } else {
        console.error('No group found with the slug "' + props.groupSlug + '"');
      }
    } else if (!props.groupSlug) {
      fetch('https://calendar.ua.edu/api/2/events/?require_all=true' + perPage + query + end)
        .then((response) => {
          return response.json();
        })
        .then((json) => {
          const _events = [];
          for (let i = 0; i < json.events.length; i++) {
            _events.push(json.events[i].event);
          }
          setEvents(_events);
        });
    }
  }, [departments]);

  const eventElements = events.map((event) => (
    <Event
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      heading_level={props.heading_level ? props.heading_level + 1 : 3}
      key={event.id}
      title={event.title}
      href={event.localist_url}
      start={event.event_instances[0].event_instance.start}
      end={event.event_instances[0].event_instance.end}
      location={event.address}
    />
  ));

  const CTA_justify =
    props.align === 'center'
      ? 'center'
      : props.align === 'right'
      ? 'end'
      : props.align === 'left'
      ? 'start'
      : undefined;

  return (
    <div className={'ua_component-wrapper ua_layout--flow' + className}>
      <Heading level={props.heading_level ? props.heading_level : 2} align={props.align}>
        {props.title}
      </Heading>
      {eventElements.length > 0 ? (
        <Grid element="ol" items={eventElements} maxColumns={props.maxColumns ? props.maxColumns : 2} />
      ) : (
        <div className="ua_event--empty">
          {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment */}
          {/* @ts-ignore */}
          <Heading level={props.heading_level ? props.heading_level + 1 : 3} align="left" className="ua_event_name">
            No upcoming events at this time
          </Heading>
          <p>Please check for more in the future</p>
        </div>
      )}
      <CallToAction href={calendarURL} text="All Events" justify={CTA_justify} />
    </div>
  );
};

export default EventFeed;
