'use client';

import { useEffect } from 'react';
import { ua_handleBreadcrumbs } from './breadcrumbs';

export interface BreadcrumbProps {
  /** The label for breadcrumb */
  label: string;
  /** The breadcrumb destination */
  href: string;
  /** Current page of breadcrumb */
  currentPage?: boolean;
}

/**
 * Breadcrumbs
 */
export const Breadcrumbs = (props: { breadcrumbList: BreadcrumbProps[] }) => {
  useEffect(() => {
    ua_handleBreadcrumbs();
  }, []);

  const breadcrumbsCount = props.breadcrumbList.length;
  let breadcrumbs = [] as BreadcrumbProps[];
  let breadcrumbOverflow = [] as BreadcrumbProps[];

  if (breadcrumbsCount <= 5) {
    breadcrumbs = props.breadcrumbList;
  } else {
    breadcrumbs = props.breadcrumbList.filter((breadcrumb, index) => index < 2 || index >= breadcrumbsCount - 2);
    breadcrumbOverflow = props.breadcrumbList.slice(2, -2);
  }

  return (
    <nav className="ua_breadcrumbs" id="UA_BreadCrumbs" aria-label="breadcrumbs">
      <ol className="ua_breadcrumbs_list">
        {breadcrumbs.map((breadcrumbItem, index) =>
          index === 2 && breadcrumbsCount > 5 ? (
            <>
              <li key={'breadcrumb_expander'}>
                <button id="overflowButton"> ... </button>
                <ol>
                  {breadcrumbOverflow.map((item, index) => (
                    <li key={`Breadcrumb_${item.label}_${index}_${item.href}`}>
                      <a href={item.href}>{item.label}</a>
                    </li>
                  ))}
                </ol>
              </li>
              <li key={`Breadcrumb_${breadcrumbItem.label}_${index}_${breadcrumbItem.href}`}>
                {breadcrumbItem.currentPage ? (
                  <span>{breadcrumbItem.label}</span>
                ) : (
                  <a href={breadcrumbItem.href}>{breadcrumbItem.label}</a>
                )}
              </li>
            </>
          ) : (
            <li key={`Breadcrumb_${breadcrumbItem.label}_${index}_${breadcrumbItem.href}`}>
              {breadcrumbItem.currentPage ? (
                <span>{breadcrumbItem.label}</span>
              ) : (
                <a href={breadcrumbItem.href}>{breadcrumbItem.label}</a>
              )}
            </li>
          )
        )}
      </ol>
    </nav>
  );
};

export default Breadcrumbs;
