import Icon from '../Icon';
import * as Types from '../../core/types';
import Link from '../../base/Link';

export interface LinkBoxProps {
  /** The title of the link */
  title: string;
  /** The link's destination */
  href: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  /** Supporting description of the link's destination */
  description?: string;
  /** Decorative icon */
  icon?: Types.icon;
  /** Custom class name */
  className?: string;
  presence?: 'default' | 'prominent';
}

/**
 * Link boxes can be used to surface content and link to other pages that may be relevant to the user, providing a preview of what the user can expect to find on that page.
 */
export const LinkBox = (props: LinkBoxProps) => {
  let className = '';
  className += props.className ? ' ' + props.className : '';
  if (props.presence === 'prominent') {
    className += ' ua_presence--prominent';
  }
  return (
    <div className={'ua_component_wrapper ' + className}>
      <div className="ua_link-box">
        {props.icon ? <Icon name={props.icon} type={props.presence === 'prominent' ? 'duotone' : 'solid'} /> : null}{' '}
        <Link href={props.href} className="ua_link-box_title" newTab={props.newTab}>
          {props.title}
        </Link>
        {props.description ? <p>{props.description}</p> : null}
      </div>
    </div>
  );
};

export default LinkBox;
