export interface BlockquoteProps {
  /** Blockquote children details */
  children: React.ReactNode;
  /** Blockquote cite url title and description */
  cite?: {
    url: string;
    title: string;
    description: string;
  };
  /** Blockquote custom class name */
  className?: string;
}

/** A blockquote indicates that the enclosed text is an extended quotation, and can be used to draw more attention to content like testimonials. */
export const Blockquote = (props: BlockquoteProps) => {
  const className = props.className ? ' ' + props.className : '';
  if ((props.cite && props.cite.title) || (props.cite && props.cite.description)) {
    return (
      <figure className={'ua_blockquote' + className}>
        <blockquote cite={props.cite.url}>{props.children}</blockquote>
        <figcaption className="ua_blockquote_cite-description">
          {props.cite.title && <cite className="ua_blockquote_cite">{props.cite.title} </cite>}
          {props.cite.description}
        </figcaption>
      </figure>
    );
  }
  return (
    <blockquote cite={props.cite?.url} className={className}>
      {props.children}
    </blockquote>
  );
};

export default Blockquote;
