import * as Types from '../../core/types';

export interface GroupProps {
  /** The group content */
  children: React.ReactNode;
  /** Any additional classes */
  className?: string;
  /** The HTML element the group should use - defaults to div */
  element?: 'div' | 'section' | 'article' | 'aside' | 'header' | 'footer';
  /** Determines how much visual weight to give the section - is ignored if background is provided */
  contrast?: 'more' | 'most';
  /** Sets the background image for the group and its overlay */
  background?: {
    img: string;
    overlay: 'dark' | 'light';
  };
  /** The width the group's container should follow - defaults to 'normal' */
  width?: Types.width;
  /** The width the group's content should follow - inherits 'width' unless specified */
  innerWidth?: Types.width;
}

export const Group = (props: GroupProps) => {
  const Element = props.element ? props.element : 'div';
  const classNames = ['ua_group', 'ua_layout--flow', props.className];
  if (props.contrast && !props.background) {
    classNames.push('ua_contrast--' + props.contrast);
  }
  if (props.background?.overlay === 'dark') {
    classNames.push('ua_contrast--most');
  }

  if (props.width == 'standard') {
    classNames.push('ua_width--standard');
  } else if (props.width == 'wide') {
    classNames.push('ua_width--wide');
  } else if (props.width == 'full') {
    classNames.push('ua_width--full');
  }

  if (props.innerWidth === 'full') {
    classNames.push('ua_layout--full');
  } else if (props.innerWidth === 'wide') {
    classNames.push('ua_layout--wide');
  } else if (props.innerWidth === 'standard') {
    classNames.push('ua_layout--standard');
  }

  if (props.background || props.contrast) {
    classNames.push('ua_has-background');
  }
  return (
    <Element className={classNames.join(' ')} data-overlay={props.background?.overlay}>
      {props.children}
      {props.background ? <img src={props.background.img} className="ua_group_background-img" alt="" /> : null}
    </Element>
  );
};

