import * as Types from '../../core/types';

export interface HeadingProps {
  /** Heading Level */
  level: Types.heading_level;
  /** Heading Text */
  children: React.ReactNode;
  /** Heading Class Name */
  className?: string;
  /** Heading Alignment */
  align?: Types.align;
}

/** Heading Component is just a utility within the Minerva project and not as a part of the component library */
export const Heading = (props: HeadingProps) => {
  const className = props.className ? ' ' + props.className : '';
  const align = props.align ? 'ua_align--' + props.align : '';
  if (props.level == 1) {
    return <h1 className={align + className}>{props.children}</h1>;
  }
  if (props.level == 2) {
    return <h2 className={align + className}>{props.children}</h2>;
  }
  if (props.level == 3) {
    return <h3 className={align + className}>{props.children}</h3>;
  }
  if (props.level == 4) {
    return <h4 className={align + className}>{props.children}</h4>;
  }
  if (props.level == 5) {
    return <h5 className={align + className}>{props.children}</h5>;
  } else {
    return <h6 className={align + className}>{props.children}</h6>;
  }
};

export default Heading;
