import { Icon } from '../Icon';
import { Heading } from '../../base/Heading';
import * as Types from '../../core/types';
import Link from '../../base/Link';

export interface EventProps {
  /** The heading level the Event should use */
  heading_level: Types.heading_level;
  /** The event title */
  title: string;
  /** Event start datetime */
  start: Date | string;
  /** Event end datetime */
  end: Date | string;
  /** A link to the event details page */
  href?: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  /** Physical or virtual location of the event */
  location?: string;
  /** Custom class name for component container */
  className?: string;
}

/** The Event component can be used to display an event's details with link to more information about the event. */
export const Event = (props: EventProps) => {
  const className = props.className ? ' ' + props.className : '';

  const eventStart = new Date(props.start);

  const eventEnd = props.end ? new Date(props.end) : null;
  const eventStartMonth = eventStart.toLocaleString('default', { month: 'short' });
  const eventStartDate = eventStart.getDate() < 10 ? '0' + eventStart.getDate() : eventStart.getDate();
  const eventStartYear = eventStart.getFullYear();
  const eventEndMonth = eventEnd ? eventEnd.toLocaleString('default', { month: 'short' }) : null;
  const eventEndDate = eventEnd ? (eventEnd.getDate() < 10 ? '0' + eventEnd.getDate() : eventEnd.getDate()) : null;
  const eventEndYear = eventEnd ? eventEnd.getFullYear() : null;
  let eventYear = null;
  eventStartYear === eventEndYear ? (eventYear = eventStartYear) : null;

  const startTime = eventStart.toLocaleTimeString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true });
  const endTime = eventEnd
    ? eventEnd.toLocaleTimeString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
    : null;

  return (
    <div className={'ua_component_wrapper' + className}>
      <article className="ua_event">
        <Heading level={props.heading_level} className={'ua_event_name'}>
          {props.href ? (
            <Link href={props.href} newTab={props.newTab}>
              {props.title}
            </Link>
          ) : (
            <>{props.title}</>
          )}
        </Heading>
        <p className="ua_event_date">
          {!props.end || (eventStartMonth == eventEndMonth && eventStartDate == eventEndDate) ? (
            <span>
              {eventStartMonth} {eventStartDate} {eventStartYear}{' '}
            </span>
          ) : eventYear ? (
            <span>
              {eventStartMonth} {eventStartDate} - {eventEndMonth} {eventEndDate} {eventYear}
            </span>
          ) : (
            <span>
              {eventStartMonth} {eventStartDate} {eventStartYear} - {eventEndMonth} {eventEndDate} {eventEndYear}
            </span>
          )}
        </p>
        <p className="ua_event_time">
          <Icon name="clock" alt="Time" /> {startTime} {endTime ? <span>- {endTime}</span> : null}
        </p>
        {props.location ? (
          <p className="ua_event_location">
            <Icon name="location-dot" alt="Location" /> {props.location}
          </p>
        ) : null}
      </article>
    </div>
  );
};

export default Event;
