import * as Types from '../../core/types';
import Link from '../../base/Link';

export type TagProps = {
  /** Tag label */
  label: string;
  /** Tag link */
  href?: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
};

export interface TagListProps {
  items: TagProps[];
  /** Alignment for the Tags */
  justify?: Types.justify;
  /** Custom class name */
  className?: string;
}

export const Tag = (props: TagProps) => {
  return (
    <>
      {props.href ? (
        <Link href={props.href} newTab={props.newTab} rel="tag">
          {props.label}
        </Link>
      ) : (
        <span>{props.label}</span>
      )}
    </>
  );
};

/** TagList is the list Tags */
export const TagList = (props: TagListProps) => {
  const justify = props.justify ? ' ua_justify--' + props.justify : '';
  const className = props.className ? ' ' + props.className : '';

  return (
    <div className={'ua_component_wrapper' + className}>
      <ul className={'ua_tag-list ' + justify}>
        {props.items.map((item) => (
          <li key={`TagList_${item.label}_${item.href}`}>
            <Tag label={item.label} href={item.href} newTab={item.newTab} />
          </li>
        ))}
      </ul>
    </div>
  );
};
