import Icon from '../Icon';
import * as Types from '../../core/types';
import Link from '../../base/Link';

export type SocialLink = {
  /** Social link label */
  label: string;
  /** Social link */
  href: string;
  /** Social link Icon */
  iconName: Types.icon;
  /** To know whether it is a Brand Icon */
  isBrand?: boolean;
};

export interface SocialLinkItemProps {
  /** Social link details */
  item: SocialLink;
  /** Determines whether to show label or not*/
  showLabel?: boolean;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
}

export interface SocialLinksProps {
  items: SocialLink[];
  /** Alignment for the social links */
  justify?: Types.justify;
  /** Determines whether to show label or not*/
  showLabel?: boolean;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  className?: string;
}

export const SocialLinkItem = (props: SocialLinkItemProps) => {
  const altText = !props.showLabel ? props.item.label : '';
  const isBrand = props.item.isBrand === undefined ? true : props.item.isBrand;
  return (
    <Link href={props.item.href} newTab={props.newTab}>
      <Icon name={props.item.iconName} type={isBrand ? 'brand' : 'solid'} alt={altText} />
      {props.showLabel ? <span> {props.item.label}</span> : null}
    </Link>
  );
};

export const SocialLinks = (props: SocialLinksProps) => {
  const justify = props.justify ? 'ua_justify--' + props.justify : '';
  const className = props.className ? ' ' + props.className : '';

  return (
    <div className={'ua_component_wrapper' + className}>
      <ul className={'ua_social-links ' + justify}>
        {props.items.map((item) => (
          <li key={`SocialLink_${item.label}_${item.href}`}>
            <SocialLinkItem item={item} showLabel={props.showLabel} newTab={props.newTab} />
          </li>
        ))}
      </ul>
    </div>
  );
};
