'use client';

import { useEffect } from 'react';
import { Icon } from '../../components/Icon';
import { PrimaryNavItem, PrimaryNavigation } from '../PrimaryNavigation';
import { ua_handleTitleBar } from './title-bar.js';

export interface TitleBarProps {
  /** The name of the website */
  title: string;
  /** Subtitle text */
  subtitle?: string;
  /** Subtitle destination */
  subtitleURL?: string;
  /** Navigation object */
  menuItems?: PrimaryNavItem[];
}

export const TitleBar = (props: TitleBarProps) => {
  useEffect(() => {
    ua_handleTitleBar();
  }, []);

  return (
    <section className="ua_title-bar" id="UA_TitleBar">
      <div className="ua_title-bar_content" id="UA_TitleBar_Content">
        <div className="ua_title-bar_title-group">
          <a href="/" className="ua_title-bar_name">
            {props.title}
          </a>
          {props.subtitle && props.subtitleURL ? (
            <a href={props.subtitleURL} className="ua_title-bar_subtitle">
              {props.subtitle}
            </a>
          ) : props.subtitle ? (
            <span className="ua_title-bar_subtitle">{props.subtitle}</span>
          ) : null}
        </div>
        <button
          type="button"
          id="UA_TitleBarExpander"
          className="ua_title-bar_expander"
          aria-expanded="false"
          aria-haspopup="true"
          aria-controls="UA_TitleBar_Search UA_PrimaryNav"
          hidden
        >
          <span className="ua_title-bar_expander_closed">
            <Icon name="bars" alt={'Expand title bar menu'} />
          </span>
          <span className="ua_title-bar_expander_open" aria-hidden="true">
            <Icon name="xmark" alt={'Close title bar menu'} />
          </span>
        </button>
        <form
          action="/search"
          method="GET"
          className="ua_input-group ua_title-bar_search"
          id="UA_TitleSearch"
          role="search"
          aria-label="Sitewide"
        >
          <label className="visually-hidden" htmlFor="UA_TitleSearch_Input">
            Search This Site
          </label>
          <input type="search" role="searchbox" name="q" id="UA_TitleSearch_Input" />
          <button type="submit">
            <Icon name="magnifying-glass" alt="Submit" />
          </button>
        </form>
      </div>
      {props.menuItems ? <PrimaryNavigation menuItems={props.menuItems} /> : null}
    </section>
  );
};

export default TitleBar;
