import Grid from '../../layout/Grid';
import { Heading } from '../../base/Heading';
import * as Types from '../../core/types';

export interface Step {
  /** Title of the Steps Item */
  title: string;
  /** Contents of the Steps Item */
  children: React.ReactNode;
}

export interface StepsProps {
  /** Step details */
  stepItems: Step[];
  /** Limits the number columns the Gird component will create */
  maxColumns?: number;
  /** The heading level the Steps Item should use */
  heading_level: Types.heading_level;
  /** Steps presence */
  presence?: 'subtle' | 'elevated';
  /** Custom class name */
  className?: string;
}

/** Steps is used to create information stepwise to follow, it contains title and content */
export const Steps = (props: StepsProps) => {
  const className = props.className ? ' ' + props.className : '';
  let presence = '';
  if (props.presence === 'subtle') {
    presence = ' ua_presence--subtle';
  } else if (props.presence === 'elevated') {
    presence = ' ua_presence--elevated';
  }
  const itemList = [] as any;
  props.stepItems.map((stepItem) =>
    itemList.push(
      <>
        <Heading level={props.heading_level} className={'ua_steps-title'}>
          {stepItem.title}
        </Heading>
        {stepItem.children}
      </>
    )
  );

  return (
    <div className={'ua_component_wrapper' + className + presence}>
      <Grid element="ol" className={'ua_steps '} items={itemList} maxColumns={props.maxColumns} />
    </div>
  );
};
