'use client';

import { useEffect } from 'react';
import { Icon } from '../../components/Icon';
import { ua_handleSecondaryNav } from './secondary-nav';

export interface SecondaryNavItem {
  label: string;
  href: string;
  children?: {
    label: string;
    href: string;
    children?: {
      label: string;
      href: string;
    }[];
  }[];
}

/** A secondary navigation element can be added to a page to help users navigate between related content. */
export const SecondaryNavigation = (props: { menuItems: SecondaryNavItem[] }) => {
  useEffect(() => {
    ua_handleSecondaryNav();
  }, []);

  return (
    <nav aria-label="Supplementary Navigation" className="ua_secondary-navigation" id="UA_SecondaryNav">
      <ul className="ua_secondary-navigation_list">
        {props.menuItems.map((menuItem) => (
          <li
            key={`secondary-nav_${props.menuItems.indexOf(menuItem)}_${menuItem.href}`}
            aria-haspopup={menuItem.children ? true : undefined}
            aria-expanded={menuItem.children ? false : undefined}
          >
            {menuItem.children ? (
              <>
                <span>
                  <a href={menuItem.href}>{menuItem.label}</a>
                  <button hidden>
                    <span className="ua_secondary-navigation_inactive_content">
                      <Icon name="caret-down" alt={'Expand ' + menuItem.label + ' menu'} />
                    </span>
                    <span className="ua_secondary-navigation_active_content">
                      <Icon name="caret-up" alt={'Close ' + menuItem.label + ' menu'} />
                    </span>
                  </button>
                </span>
                <ul aria-hidden="false" aria-label={menuItem.label + ' submenu'}>
                  {menuItem.children.map((nestedItem) => (
                    <li
                      key={`secondary-nav_sub-menu_${menuItem.children?.indexOf(nestedItem)}_${nestedItem.href}`}
                      aria-haspopup={nestedItem.children ? true : undefined}
                      aria-expanded={nestedItem.children ? true : undefined}
                    >
                      {nestedItem.children ? (
                        <>
                          <span>
                            <a href={nestedItem.href}>{nestedItem.label}</a>
                            <button hidden>
                              <span className="ua_secondary-navigation_inactive_content">
                                <Icon name="caret-down" alt={'Expand ' + nestedItem.label + ' menu'} />
                              </span>
                              <span className="ua_secondary-navigation_active_content">
                                <Icon name="caret-up" alt={'Close ' + nestedItem.label + ' menu'} />
                              </span>
                            </button>
                          </span>
                          <ul aria-hidden="false" aria-label={nestedItem.label + ' submenu'}>
                            {nestedItem.children.map((deepItem) => (
                              <li
                                key={`secondary-nav_sub-sub-menu_${nestedItem.children?.indexOf(deepItem)}_${
                                  deepItem.href
                                }`}
                              >
                                <span>
                                  <a href={deepItem.href} data-text={deepItem.label}>
                                    {deepItem.label}
                                  </a>
                                </span>
                              </li>
                            ))}
                          </ul>
                        </>
                      ) : (
                        <span>
                          <a href={nestedItem.href}>{nestedItem.label}</a>
                        </span>
                      )}
                    </li>
                  ))}
                </ul>
              </>
            ) : (
              <span>
                <a href={menuItem.href}>{menuItem.label}</a>
              </span>
            )}
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default SecondaryNavigation;
