import type { icon } from '../../core/types'; // This is for the icon type
import { fa_standard_icons, fa_brand_icons } from '../../../lib/icons'; // This is for the arrays you can loop through
import { Grid } from '../../layout/Grid';

export interface IconProps {
  /** The name of Icon */
  name: icon;
  /** The alt text of Icon */
  alt?: string;
  type?: 'solid' | 'brand' | 'duotone';
}

/** The Icon component can be used to display icon. */
export const Icon = (props: IconProps) => {
  const fa_icon_type_css = props.type === 'brand' ? 'fa-brands' : props.type === 'duotone' ? 'fa-duotone' : 'fa';
  let isIconAvailable = false;

  function iconNotAvailableMsg() {
    console.error(
      `Error: Invalid icon name. ` + props.name + ` is not a valid icon in the ` + fa_icon_type_css + ` icon set.`
    );
  }

  //Check if icon is available in brand or standard icons
  if (props.type === 'brand') {
    isIconAvailable = fa_brand_icons.some((icon) => icon === props.name);
  } else {
    isIconAvailable = fa_standard_icons.some((icon) => icon === props.name);
  }

  if (!isIconAvailable) {
    iconNotAvailableMsg();
  }

  if (props.alt) {
    return (
      <>
        <span className={`${fa_icon_type_css} fa-${props.name}`} title={props.alt} aria-hidden="true"></span>
        <span className="visually-hidden">{props.alt}</span>
      </>
    );
  }
  return <span className={`${fa_icon_type_css} fa-${props.name}`} aria-hidden="true"></span>;
};

export const StandardIconList = () => {
  const standardIconsList = [] as any;
  fa_standard_icons.map((icon) =>
    standardIconsList.push(
      <>
        <Icon name={icon} alt={icon} />
        <span style={{ font: 'var(--ua_typeset--xsmall)' }}>{icon}</span>
      </>
    )
  );
  return <Grid element="ul" className="ua_icon-list" items={standardIconsList} />;
};

export const BrandIconList = () => {
  const brandIconsList = [] as any;
  fa_brand_icons.map((icon) =>
    brandIconsList.push(
      <>
        <Icon name={icon} alt={icon} type={'brand'} />
        <span style={{ font: 'var(--ua_typeset--xsmall)' }}>{icon}</span>
      </>
    )
  );
  return <Grid element="ul" className="ua_icon-list" items={brandIconsList} />;
};

export default Icon;
