export interface LeadTextProps {
  /** LeadText text */
  text: string;
  /** LeadText custom class */
  className?: string;
}

/** A LeadText indicates that text is highlight, that can be used to draw more attention to content */
export const LeadText = (props: LeadTextProps) => {
  const className = props.className ? ' ' + props.className : '';

  return (
    <div className={'ua_component-wrapper' + className}>
      <p className="ua_lead-in">
        <strong>{props.text}</strong>
      </p>
    </div>
  );
};

export default LeadText;
