import * as Types from '../../core/types';
import { Icon } from '../..';
import { Link } from '../..';
import { Grid } from '../..';

export interface LinkItemProps {
  /** Title of the list item */
  title: string;
  /** URL for the list item */
  href: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  /** Icon for the list item */
  icon?: Types.icon;
  /** Max number of columns */
}

export interface LinkListProps {
  /** List of links */
  list: LinkItemProps[];
  /** Max number of columns */
  maxColumns?: number;
  /** Custom class name */
  className?: string;
}

export const LinkItem = (props: LinkItemProps) => {
  return (
    <Link href={props.href} className="ua_link-list_item" newTab={props.newTab}>
      {props.icon ? <Icon name={props.icon} alt={props.icon} /> : null}
      {' ' + props.title}
    </Link>
  );
};

/** Link List can be used to surface content and link to other pages that may be relevant to the user. */
export const LinkList = (props: LinkListProps) => {
  const className = props.className ? ' ' + props.className : '';

  const items = props.list.map((listItem) => (
    <LinkItem key="" title={listItem.title} href={listItem.href} icon={listItem.icon} />
  ));

  return (
    <div className={'ua_component_wrapper' + className}>
      <Grid element="ul" className="ua_link-list" items={items} maxColumns={props.maxColumns} />
    </div>
  );
};
