import { Heading } from '../../base/Heading';
import * as Types from '../../core/types';
import Link from '../../base/Link';

export interface CardProps {
  /** The heading level the card should use */
  heading_level: Types.heading_level;
  /** The card title text */
  title: string;
  /** The sub title for card */
  subTitle?: string;
  /** The card link destination */
  href?: string;
  /** Determines if the link opens in a new tab */
  newTab?: boolean;
  /** Information about the destination's content */
  description?: React.ReactNode;
  /** Card image props */
  img?: {
    src: string;
    alt?: string;
    aspectRatio?: Types.aspect_ratio;
  };
  /** Card presence */
  presence?: 'subtle';
  /** Card text alignment */
  align?: Types.align;
  /** Horizontal card layout */
  isLandscape?: boolean;
  /** Card custom class */
  className?: string;
}

/** Card can be used to surface content and link to other pages that may be relevant to the user, providing a preview of what the user can expect to find on that page. */
export const Card = (props: CardProps) => {
  const className = props.className ? ' ' + props.className : '';
  const alt = props.img && props.img.alt ? props.img.alt : '';
  let presence = '';
  if (props.presence === 'subtle') {
    presence = ' ua_presence--subtle';
  }
  const orientation = props.isLandscape ? ' ua_card--landscape' : '';
  const align = props.align ? ' ua_align--' + props.align : '';

  if (props.img && props.isLandscape && props.img.aspectRatio) {
    console.warn('Card: Using aspectRatio with isLandscape set as true is not valid. Aspect ratio will be ignored.');
  }

  return (
    <div className={'ua_component_wrapper' + className + presence}>
      <article className={'ua_card' + orientation + align}>
        <div className="ua_card_content-wrapper">
          <Heading level={props.heading_level} className={'ua_card_title'}>
            {props.href ? (
              <Link href={props.href} newTab={props.newTab}>
                {props.title}
              </Link>
            ) : (
              props.title
            )}
          </Heading>
          <span className="ua_card_subtitle">{props.subTitle}</span>
          {props.description ? props.description : null}
        </div>

        {props.img ? (
          <div className="ua_card_image-wrapper">
            {props.isLandscape ? (
              <img src={props.img.src} alt={alt} />
            ) : (
              <img src={props.img.src} alt={alt} style={{ aspectRatio: props.img.aspectRatio }} />
            )}
          </div>
        ) : null}
      </article>
    </div>
  );
};

export default Card;
